.wp-block{
    max-width: calc( #{$container-single} + 100px );
    &[data-align=wide] {
        max-width: calc( ( #{$container-single} * 1.3 ) + 100px );
    }
    &[data-align=full],
    &.alignfull {
        max-width: none;
    }
}